import React, { useState } from "react";
import { Button, Container } from "react-bootstrap";
import CreateBrand from "../components/modals/CreateBrand";
import CreateDevice from "../components/modals/CreateDevice";

const Admin = function () {
    const [isModal, setIsModal] = useState(false);
    const [isModalBrand, setIsModalBrand] = useState(false);

    return (
        <Container className="d-flex flex-column p-5">
            <Button onClick={() => setIsModalBrand(true)} className="mt-2" variant="outline-dark"> </Button>
            <Button onClick={() => setIsModal(true)} className="mt-3" variant="outline-dark"> </Button>
            <CreateDevice show={isModal} onHide={setIsModal} />
            <CreateBrand show={isModalBrand} onHide={setIsModalBrand} />
        </Container>
    );
}

export default Admin;