import React, { useContext, useState } from "react";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import { observer } from "mobx-react-lite";
import { Context } from "../..";
import { path_name } from "../../const";
import axios from "axios"

const CreateBrand = observer(({ show, onHide }) => {
    const [value, setValue] = useState('');
    const { device } = useContext(Context);

    const handleClose = () => {
        setValue('');
        onHide(false);
    }

    const addBrand = () => {
        axios.post(path_name + 'brands', { id: Date.now(), name: value }).then((res) => { device.setBrands([...device.brands, { id: Date.now(), name: value }]) });
        handleClose();
    }

    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Control value={value} onChange={e => setValue(e.target.value)} placeholder=" "></Form.Control>
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    
                </Button>
                <Button variant="primary" onClick={addBrand}>
                    
                </Button>
            </Modal.Footer>
        </Modal>
    );
})

export default CreateBrand;