import { makeAutoObservable } from "mobx";

export default class BasketStore {
    constructor() {
        this._devices = [];
        makeAutoObservable(this);
    }

    addDevice(device) {
        let isBeen = false;
        for (let i of this._devices) {
            if (i.id === device.id) {
                i.count += 1;
                isBeen = true;
            }
        }
        if (!isBeen) {
            this._devices.push({ ...device, count: 1 });
        }
    }

    deleteDevice(id) {
        this._devices = this._devices.filter(dev => dev.id !== id);
    }

    changeCount(id, value) {
        for (let i of this._devices) {
            if (i.id === id) {
                i.count += value;
                if (i.count === 0) {
                    this.deleteDevice(i.id);
                }
            }
        }
    }

    get devices() {
        return this._devices;
    }
}