import { observer } from "mobx-react-lite";
import React, { useContext } from "react";
import { Button } from "react-bootstrap";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { NavLink, useNavigate } from "react-router-dom";
import { Context } from "..";
import basket from "../imgs/basket.svg"


const NavBar = observer(function () {
    const { user } = useContext(Context);
    const root = useNavigate();

    const outLogin = (e) => {
        e.preventDefault();
        if (window.confirm('   ?')) {
            localStorage.setItem('auth', '');
            user.setIsAuth(false);
            root('/login');
        }
    }

    return (
        <Navbar bg="light" variant="dark">
            <Container>
                <NavLink style={{ color: 'black', 'textDecoration': 'none', 'fontSize': '20px', 'fontWeight': '700' }} to="">PhoneShop</NavLink>
                <Nav>
                    <NavLink className="m-2" to="/basket">
                        <img className="basket" src={basket} alt="" />
                    </NavLink>
                    {user.isAuth ?
                        <>
                            <Button variant="dark" onClick={() => root('/admin')}> </Button>
                            <Button variant="dark" onClick={outLogin} className="ms-2"></Button>
                        </>
                        :
                        <>
                            <Button onClick={() => root('/login')}></Button>
                        </>
                    }
                </Nav>
            </Container>
        </Navbar>
    );
})

export default NavBar;