import { observer } from "mobx-react-lite";
import React, { useContext } from "react";
import { Context } from "..";
import DeviceItem from "./DeviceItem";

const DeviceList = observer(function () {
    const { device } = useContext(Context)
    const devices = device.selected.name ? device.devices.filter(dev => device.selected.name === dev.brand) : device.devices;

    return (
        <div className="row">
            {devices.map(dev =>
                <DeviceItem device={dev} key={dev.id}></DeviceItem>
            )}
        </div>
    );
})

export default DeviceList;