import React from "react";
import { Image } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import iphone from "../imgs/iphone.png"

const DeviceItem = function ({ device }) {
    const rout = useNavigate()

    return (
        <div className="item" onClick={() => rout("/devices/" + device.id)}>
            <div className="item-container">
                <Image src={device.image} className="item__img" />
            </div>
            <div style={{ textAlign: 'center' }}>{device.name}</div>
            <div style={{ textAlign: 'center' }}>{device.price + '$'}</div>
        </div>
    );
}

export default DeviceItem;