import React, { useContext, useEffect, useState } from "react";
import { Context } from "..";
import { observer } from "mobx-react-lite";
import { Container } from "react-bootstrap";
import BasketItem from "../components/BasketItem";
import { useSpring, animated } from '@react-spring/web'

const Basket = observer(() => {
    const { basket } = useContext(Context);
    const [count, setCount] = useState(0);
    const [price, setPrice] = useState(0);
    const [state, toggle] = useState(true);
    const { x } = useSpring({
        x: state ? 1 : 0,
        config: { duration: 500 },
    })

    useEffect(() => {
        const total = basket.devices.reduce((a, b) => a + b.count, 0);
        setCount(total);
    }, [basket.devices])

    useEffect(() => {
        const pr = basket.devices.reduce((a, b) => a + (b.price - 0) * b.count, 0);
        setPrice(pr);
    }, [basket.devices])

    return (
        <Container>
            {basket.devices.length ?
                <div>
                    <div className="basket-title"> </div>
                    <div className="basket-row">
                        <span /><span> </span><span>o</span><span></span><span />
                    </div>
                    {basket.devices.map(dev => {
                        return <BasketItem
                            device={dev}
                            key={dev.id}
                            setTotalCount={setCount}
                            setTotalPrice={setPrice}
                            triger={toggle}
                        />
                    }
                    )}
                    <div className="basket-row-footer">
                        <span />
                        <span />
                        <animated.span
                            style={{
                                scale: x.to({
                                    range: [0, 0.5, 1],
                                    output: [1, 1.2, 1],
                                })
                            }}
                        >
                            {count}
                        </animated.span>
                        <animated.span
                            style={{
                                scale: x.to({
                                    range: [0, 0.5, 1],
                                    output: [1, 1.2, 1],
                                })
                            }}
                        >
                            {price}$
                        </animated.span>
                        <span />
                    </div></div>
                :
                <h1 style={{ 'textAlign': 'center', 'marginTop': '250px' }}>  </h1>
            }
        </Container >
    );
})

export default Basket;