import React, { useContext } from "react";
import iphone from "../imgs/iphone.png"
import { observer } from "mobx-react-lite";
import { Context } from "..";
import arrow from '../imgs/arrow.png'

const BasketItem = observer(({ device, setTotalCount, setTotalPrice, triger }) => {
    const { basket } = useContext(Context);

    const increase = () => {
        setTotalCount((prev) => prev + 1);
        setTotalPrice((prev) => prev + (device.price - 0));
        basket.changeCount(device.id, 1);
        triger((prev) => !prev);
    }

    const decrease = () => {
        setTotalCount((prev) => prev - 1);
        setTotalPrice((prev) => prev - (device.price - 0));
        basket.changeCount(device.id, -1);
        triger((prev) => !prev);
    }

    return (
        <div className="basket-item-row">
            <div className="basket-item item-image-container"><img src={device.image} className="basket-item-img" alt="" /></div>
            <div className="basket-item-name basket-item">{device.name}</div>
            <div className="basket-item count">
                <div className="basket-item-counter">{device.count}</div>
                <div className="basket-item-arrows">
                    <div onClick={increase} className="arrow-up"><img src={arrow} alt='' /></div>
                    <div onClick={decrease} className="arrow-down"><img src={arrow} alt='' /></div>
                </div>
            </div>
            <div className="basket-item-price basket-item">{device.price}$</div>
            <div onClick={() => { basket.deleteDevice(device.id) }} className="basket-item-cancel basket-item"></div>
        </div>
    );
})

export default BasketItem;