import React, { useContext, useState } from "react";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import { observer } from "mobx-react-lite";
import { Context } from "../..";
import { Col, Row } from "react-bootstrap";
import { path_name } from "../../const";
import axios from "axios"

const CreateDevice = observer(({ show, onHide }) => {
    const { device } = useContext(Context);
    const [info, setInfo] = useState([]);
    const [brand, setBrand] = useState('');
    const [name, setName] = useState('');
    const [price, setPrice] = useState('');
    const [imgLink, setImgLink] = useState('');

    const handleClose = () => {
        setInfo([]);
        setBrand('');
        setName('');
        setPrice('');
        setImgLink('');
        onHide(false);
    };

    const addDevice = () => {
        const newPhone = {
            id: Date.now(),
            price,
            brand,
            name,
            rating: (Math.random() * 4 + 1).toFixed(2),
            description: info,
            image: imgLink
        }
        axios.post(path_name + 'phones', newPhone).then((res) => { device.setBrands([...device.devices, newPhone]) });
        handleClose();
    }

    const addInfo = () => {
        setInfo([...info, { title: '', description: '', id: Date.now() }])
    }

    const removeChem = (elem) => {
        setInfo(info.filter(i => i.id !== elem.id))
    }

    const changeInfo = (key, value, id) => {
        setInfo(info.map(el => el.id === id ? { ...el, [key]: value } : el));
    }

    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Select value={brand} onChange={(e) => setBrand(e.target.value)}>
                        <option key={0}> </option>
                        {device.brands.map(brand =>
                            <option key={brand.id} value={brand.name}>{brand.name}</option>
                        )}
                    </Form.Select>
                    <Form.Control
                        placeholder="  "
                        className="mt-2"
                        value={name}
                        onChange={e => setName(e.target.value)}
                    />
                    <Form.Control
                        placeholder="   "
                        className="mt-2"
                        value={imgLink}
                        onChange={e => setImgLink(e.target.value)}
                    />
                    <Form.Control
                        placeholder="  "
                        type="number"
                        className="mt-2"
                        value={price}
                        onChange={e => setPrice(e.target.value)}
                    />
                    <hr />
                    <Button onClick={addInfo}>
                          
                    </Button>
                    {
                        info.map(i =>
                            <Row key={i.id} className="m-2">
                                <Col md={4}>
                                    <Form.Control
                                        placeholder=""
                                        value={i.title}
                                        onChange={(e) => changeInfo('title', e.target.value, i.id)}
                                    />
                                </Col>
                                <Col md={4}>
                                    <Form.Control
                                        placeholder=""
                                        value={i.description}
                                        onChange={(e) => changeInfo('description', e.target.value, i.id)}
                                    />
                                </Col>
                                <Col md={4}>
                                    <Button onClick={() => removeChem(i)} variant={"outline-danger"}>
                                        
                                    </Button>
                                </Col>
                            </Row>
                        )
                    }
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    
                </Button>
                <Button variant="primary" onClick={addDevice}>
                    
                </Button>
            </Modal.Footer>
        </Modal>
    );
})

export default CreateDevice;