import { makeAutoObservable } from "mobx";

export default class DevicesStore {
    constructor() {
        this._brands = [];
        this._devices = [];
        this._selected = {};
        makeAutoObservable(this)
    }

    setBrands(brands) {
        this._brands = brands;
    }

    setDevices(devices) {
        this._devices = devices;
    }

    setSelected(brand) {
        this._selected = brand;
    }

    get devices() {
        return this._devices;
    }

    get brands() {
        return this._brands;
    }

    get selected() {
        return this._selected;
    }
}