import React, { useContext, useEffect } from "react";
import { Context } from "..";
import ListGroup from 'react-bootstrap/ListGroup';
import { observer } from "mobx-react-lite";
import axios from "axios"
import { path_name } from "../const"

const BrandBar = observer(function () {
    const { device } = useContext(Context);

    useEffect(() => {
        axios.get(path_name + 'brands').then((res) => { device.setBrands(res.data) });
        axios.get(path_name + 'phones').then((res) => { device.setDevices(res.data) });
    }, [])

    return (
        <ListGroup>
            <ListGroup.Item
                key={0}
                onClick={() => device.setSelected({ id: 0 })}
                style={{ cursor: 'pointer' }}
            >
                 
            </ListGroup.Item>
            {device.brands.map((bran) =>
                <ListGroup.Item
                    active={bran.id === device.selected.id}
                    key={bran.id}
                    onClick={() => device.setSelected(bran)}
                    style={{ cursor: 'pointer' }}
                >
                    {bran.name}
                </ListGroup.Item>
            )}
        </ListGroup>
    );
})

export default BrandBar;