import { observer } from "mobx-react-lite";
import React, { useContext } from "react";
import { Container, Col, Image, Row, Button } from "react-bootstrap";
import { useLocation, useNavigate } from "react-router-dom";
import { Context } from "..";
import iphone from "../imgs/iphone.png"
import star from "../imgs/star.png"

const DevicePage = observer(() => {
    const { pathname } = useLocation();
    const { device, basket } = useContext(Context);
    const rout = useNavigate();
    const dev = device.devices.filter(d => d.id === pathname.replace(/[^0-9]/g, ''))[0];

    return (
        <Container>
            <Row className="mt-5">
                <Col md={4}>
                    <h2 style={{ 'textAlign': 'center' }}>{dev.name}</h2>
                    <Image src={dev.image} width={300} height={300} />
                </Col>
                <Col md={4}>
                    <Row>
                        <div
                            className="d-flex justify-content-center align-items-center p-5"
                            style={{ 'background': `url(${star}) no-repeat center`, width: '300px', height: '300px' }}
                        >
                            {dev.rating}
                        </div>
                    </Row>
                </Col>
            </Row>
            <Col style={{ textAlign: 'center' }}>
                <h2>{dev.price + "$"}</h2>
                <Button onClick={() => { basket.addDevice(dev); rout('/basket') }}>  </Button>
            </Col>
            <Row className="d-flex flex-column m-5">
                <h1></h1>
                {dev.description.map((info, index) =>
                    <Row key={info.id} style={{ background: index % 2 ? 'transparent' : 'lightgray' }}>
                        {info.title}: {info.description}
                    </Row>
                )}
            </Row>
        </Container>
    );
})

export default DevicePage;